import React, { useState } from 'react';

import { Link } from "react-router-dom";
import { CardMedia, Chip, makeStyles } from '@material-ui/core';
import _ from 'lodash'

import { CalendarIcon } from '../components/icons'
import data from '../data/data'

export default function LastPost() {
  const [category, setCategory] = useState(null);
  const classes = useStyles();
  function filterCategory(category) {
    setCategory(category);
  }
  return (
    <>
      <div style={{ padding: 10, margin: 10 }}>
        {
          _.unionBy(data, 'category').map((item, i) =>
            <Chip className={classes.chip} label={item.category} key={i} onClick={() => filterCategory(item.category)} />
          )}
      </div>
      {
        data
          .filter(item => item.category === category || category === null)
          .map(item => (
            <div key={item.seoUrl} className={classes.root}>
              <Link style={{ textDecoration: 'none' }} to={`/blog/${item.seoUrl}`}>
                <div >
                  <img className={classes.image} src='code.png'  alt="my-img"/>
                 
                 </div>
                <h1 className={classes.title}>{item.title}</h1>
                        
          <span><CalendarIcon className={classes.calendarIcon} /></span>
          <span className={classes.date}>{item.date}</span>
              </Link>
            </div>
          )
          )}
    </>
  );
}
const useStyles = makeStyles({
  root: {
    minWidth: 275,
    marginBottom: 20,
    borderRadius: 8,
    border: '1px solid #dadce0',
    '&:hover': {
      boxShadow: "1px 1px 1px 1px rgba(60,64,67,0.3)",
    },

  },
  date: {
    color: '#6d6d6d',
    paddingLeft: 10,
    fontWeight:'500',
    verticalAlign:'12%'
  },
  calendarIcon:{
    width: 18,
    height: 19,
    paddingLeft: 20,
  },
  chip: {
    marginLeft: 10,
    marginBottom: 10,
    color: 'black',
    backgroundColor: '#D5D4D2',
    '&:focus': {
      color: "white",
      backgroundColor: '#40AEF8',
    }
  },
  image: {
    width: '100%',
    borderTopLeftRadius:8,
    borderTopRightRadius:8
  },
  title: {
    paddingLeft: 20,
    paddingTop:10,
    color: '#3d3d3d',
    fontWeight: '600',
    fontSize: 21,
  }

});
